<template>
  <view class="team-expense-container">
    <u-form :model="form" ref="uForm">
      <u-form-item label="开始日期">
        <u-datetime-picker
          v-model="form.startDate"
          mode="date"
          :max-date="new Date().getTime()"
        ></u-datetime-picker>
      </u-form-item>
      <u-form-item label="结束日期">
        <u-datetime-picker
          v-model="form.endDate"
          mode="date"
          :max-date="new Date().getTime()"
        ></u-datetime-picker>
      </u-form-item>
      <u-form-item label="状态">
        <!-- <u-select v-model="form.status" :list="statusOptions"></u-select> -->
      </u-form-item>
    </u-form>
    <u-button @click="handleSearch" type="primary">查询</u-button>

    <view class="team-expense-list" v-if="teamExpenses.length > 0">
      <view
        class="team-expense-item"
        v-for="(expense, index) in teamExpenses"
        :key="index"
      >
        <text>员工姓名：{{ expense.name }}</text>
        <text>报销类型：{{ expense.type }}</text>
        <text>金额：{{ expense.amount }}</text>
        <text>状态：{{ getStatusText(expense.status) }}</text>
        <text>提交日期：{{ formatDate(expense.date) }}</text>
        <u-button
          v-if="expense.status === 'pending'"
          @click="handleApprove(expense._id)"
          type="primary"
          size="mini"
          >审批</u-button
        >
      </view>
    </view>
    <view v-else>暂无数据</view>

    <u-loadmore :status="loadMoreStatus" @loadmore="loadMore" />
  </view>
</template>

<script>
export default {
  data() {
    return {
      form: {
        startDate: new Date(
          new Date().setDate(new Date().getDate() - 30)
        ).getTime(), // 默认查询最近30天
        endDate: new Date().getTime(),
        status: "",
      },
      statusOptions: [
        { label: "全部", value: "" },
        { label: "待审核", value: "pending" },
        { label: "已通过", value: "approved" },
        { label: "已拒绝", value: "rejected" },
      ],
      teamExpenses: [],
      page: 1,
      pageSize: 20,
      loadMoreStatus: "loadmore",
      userInfo: null,
    };
  },
  onLoad() {
    this.userInfo = uni.getStorageSync("userInfo");
    if (!this.userInfo || !this.userInfo.isAdmin) {
      uni.showToast({
        title: "无权限访问",
        icon: "none",
      });
      uni.navigateBack();
      return;
    }
    this.handleSearch();
  },
  methods: {
    formatDate(timestamp) {
      const date = new Date(timestamp);
      return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(
        2,
        "0"
      )}-${String(date.getDate()).padStart(2, "0")}`;
    },
    getStatusText(status) {
      const statusMap = {
        pending: "待审核",
        approved: "已通过",
        rejected: "已拒绝",
      };
      return statusMap[status] || status;
    },
    handleSearch() {
      this.page = 1;
      this.teamExpenses = [];
      this.loadMoreStatus = "loadmore";
      this.fetchTeamExpenses();
    },
    fetchTeamExpenses() {
      uniCloud
        .callFunction({
          name: "queryTeamExpenses",
          data: {
            startDate: this.form.startDate,
            endDate: this.form.endDate,
            status: this.form.status,
            page: this.page,
            pageSize: this.pageSize,
          },
        })
        .then(res => {
          if (res.result.code === 0) {
            this.teamExpenses = [...this.teamExpenses, ...res.result.data];
            if (res.result.data.length < this.pageSize) {
              this.loadMoreStatus = "nomore";
            } else {
              this.loadMoreStatus = "loadmore";
            }
          } else {
            uni.showToast({
              title: res.result.message,
              icon: "none",
            });
          }
        })
        .catch(err => {
          uni.showToast({
            title: "查询失败，请重试",
            icon: "none",
          });
        });
    },
    loadMore() {
      if (this.loadMoreStatus === "loadmore") {
        this.page++;
        this.fetchTeamExpenses();
      }
    },
    handleApprove(expenseId) {
      uni.showActionSheet({
        itemList: ["通过", "拒绝"],
        success: res => {
          const status = res.tapIndex === 0 ? "approved" : "rejected";
          this.approveExpense(expenseId, status);
        },
      });
    },
    approveExpense(expenseId, status) {
      uniCloud
        .callFunction({
          name: "approveExpense",
          data: {
            expenseId,
            status,
          },
        })
        .then(res => {
          if (res.result.code === 0) {
            uni.showToast({
              title: "审批成功",
              icon: "success",
            });
            // 刷新列表
            this.handleSearch();
          } else {
            uni.showToast({
              title: res.result.message,
              icon: "none",
            });
          }
        })
        .catch(err => {
          uni.showToast({
            title: "审批失败，请重试",
            icon: "none",
          });
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.team-expense-container {
  padding: 20px;
}
.team-expense-list {
  margin-top: 20px;
}
.team-expense-item {
  background-color: #f8f8f8;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}
</style>
